import {
  Scroller,
  Flexbox,
  Toast,
  FlexboxItem,
  XButton,
  Group,
  Cell,
  Timeline,
  TimelineItem
} from "vux"
import {
  global
} from '../assets/javascript/global.js'
import {
  request
} from '../api/api.js'
import store from '../store.js'
export default {
  components: {
    Scroller,
    Toast,
    Flexbox,
    FlexboxItem,
    XButton,
    Group,
    Cell,
    Timeline,
    TimelineItem
  },
  data() {
    return {
      showMsg: false,
      isNoData: false,
      text: '',
      type: '',
      exams: [],
      examSoon: []
    }
  },
  methods: {
    onItemClick(index) {
      // console.log(this.examSoon.examSite);
      if (this.examSoon.examSite) {
        this.$router.push({
          path: '/examPosition',
          query: {
            examSiteName: this.examSoon.examSite
          }
        });
      }
    },
    getExamInfo() {
      let object = JSON.parse(sessionStorage.getItem('token'));
      if (object) {
        let args = [object.schoolCode.replace(/'/g, ''), object.cardNo.replace(/'/g, '')];
        request.ExamManager.studentExamManager(args).then(res => {
          if (res.success) {
            if (res.object.length) {
              this.exams = res.object;
              res.object.forEach((item, index) => {
                if (!index.examDate && !item.examScore) {
                  this.examSoon = item;
                  this.exams.splice(index, 1);
                }
                item.isPassed = item.examStageName == '科目二' ? (item.examScore >= 80 ? true : false) : (item.examScore >= 90 ? true : false);
              });
            } else {
              this.isNoData = true;
            }
            store.commit('LOADED');
          } else {
            store.commit('LOADED');
            this.type = 'text';
            this.showMsg = true;
            this.text = res.message;
          }
        });
      } else {
        store.commit('LOADED');
        this.type = 'warn';
        this.text = '请先登录';
        this.showMsg = true;
      }
    }
  },
  created() {
    // store.commit('LOADED');
    this.getExamInfo();
  }
}



