
import Msg from 'vux/src/components/msg/index.vue'

import store from '../store.js'
export default {
components: {
Msg
},
data() {
return {
icon: ''
}
},
methods: {
changeIcon() {
if (!this.icon || this.icon === 'success') {
this.icon = 'warn';
}
else if (this.icon === 'warn') {
this.icon = 'info';
}
else if (this.icon === 'info') {
this.icon = 'waiting';
}
else if (this.icon === 'waiting') {
this.icon = 'success';
}
else {
this.icon = 'success';
}
}
},
created() {

store.commit('LOADED');
}
}



