
import Toast from 'vux/src/components/toast/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Sticky from 'vux/src/components/sticky/index.vue'

import {
global
} from '../assets/javascript/global.js'
import {
request
} from '../api/api.js'
import store from '../store.js'
import FileUpload from "../components/common/FileUpload.vue";
export default {
components: {
Scroller,
Toast, XButton, Checker, CheckerItem, XTextarea, Sticky,
"fileupload": FileUpload
},
data() {
return {
type: "",
checked: [],
allTags: [
{
key: 'COMPLAINT_TEACHERS',
value: '投诉教练'
},
{
key: 'COMPLAINT_WORKS',
value: '投诉工作人员'
},
{
key: 'SUGGESTION_EX',
value: '建议用户体验'
},
{
key: 'SUGGESTION_LOSE',
value: '建议功能缺失'
},
{
key: 'SUGGESTION_OTHER',
value: '其他'
}
],
feedMsg: '',
userInfo: JSON.parse(sessionStorage.getItem('token')),
messages: "",
showMsg: false,
uploadList: []
}
},
methods: {
onTextFocus() {





},
submit() {
if (this.userInfo) {
let feedMsg = this.feedMsg;
if (feedMsg === "") {
this.type = "text";
this.messages = "请填写反馈内容";
this.showMsg = true;
}
else if(!this.checked.length){
this.type = "text";
this.messages = "请选择意见类别标签";
this.showMsg = true;
}
else {









let args = {
openId: this.userInfo.openId,
studentId: this.userInfo.studentId,
csContent: this.feedMsg,
csImg: this.uploadList[0],
userTypeEnum: 'STUDENT',
schoolCode: this.userInfo.schoolCode,
csTypeEnum: this.checked
};

request.feedback.ComplaintSuggestion(args).then(res => {
if (res.success) {
this.feedMsg = "";
this.checked = [];
this.uploadList = [];
this.type = "success";
this.messages = "反馈提交成功";
} else {
this.type = "warn";
this.messages = "反馈提交失败";
}
this.showMsg = true;
});
}
}
else {
this.type = "text";
this.messages = "请先登录";
this.showMsg = true;
}
},
startUpload(e) {

},
finishUpload(e) {
let status = e.target.status;
let response = e.target.response;
if (status === 200) {
let result = JSON.parse(response);
if (result.success) {
this.uploadList.push(result.object[0])
}
}
},
deleteItem(index) {
this.uploadList.splice(index, 1);
}
},
created() {
this.uploadList = [];
store.commit('LOADED');
}
}



