import {
  Box,
  XButton,
  Toast,
  Group,
  Countdown
} from "vux"
import {
  global
} from '../assets/javascript/global.js'
import {
  request
} from '../api/api.js'
import store from '../store.js'

export default {
  components: {
    Box,
    Group,
    Toast,
    XButton,
    Countdown
  },
  data() {
    return {
      mobile: "",
      showMsg: false,
      showCount: false,
      isClick: true,
      text: '',
      checkCode: '',
      openId: '',
      redirectUrl: '',
      type: '',
      time: 120
    }
  },
  methods: {
    finish() {
      this.showCount = false;
    },
    enterCode(v) {
      if (this.checkCode.length === 6) this.isClick = false;
    },
    // 获取验证码
    getCheckCode() {
      // this.showCount = true;
      let args = {
        phone: this.mobile
      };
      if (args.phone.length > 0) {
        if (global.fieldVerification.IsPhone(Number(args.phone))) {
          request.Authorize.verificationCode(args).then(res => {
            if (res.success) {
              this.showCount = true;
              this.type = 'text';
              this.text = '短信验证码已发送';
              this.showMsg = true;
            } else {
              this.type = 'text';
              this.text = res.message;
              this.showMsg = true;
            }
          });
        } else {
          this.type = 'text';
          this.text = '请输入正确的电话号码';
          this.showMsg = true;
        }
      } else {
        this.type = 'text';
        this.text = '手机号不能为空';
        this.showMsg = true;
      }
    },
    // 绑定用户手机
    bindInfo() {
      const args = {
        phone: this.mobile,
        code: this.checkCode,
        openId: this.openId,
        redirectURL: this.redirectUrl
      }
      if (!args.phone) {
        this.type = 'text';
        this.showMsg = true;
        this.text = '手机号码不能为空';
      } else if (!global.fieldVerification.IsPhone(Number(args.phone))) {
        this.type = 'text';
        this.text = '请输入正确的手机号码';
        this.showMsg = true;
      } else {
        request.Authorize.wx_Infobind(args).then(res => {
          // global.printLog(res);
          if (res.success) {
            let tmpObj = res.object.loginUser;
            tmpObj.openId = res.object.openId;
            tmpObj.redirectUrl = res.object.redirectUrl;
            sessionStorage.setItem('token', JSON.stringify(tmpObj));
            this.$router.push({
              path: '/'
            });
          } else {
            this.type = 'text';
            this.showMsg = true;
            this.text = res.message;
          }
        });
      }
    },
    getDataFromUrl() {
      var path = this.$route;
      // console.log(path);
      store.commit('LOADED');
      this.openId = path.query.openId;
      this.redirectUrl = path.query.redirectUrl;
    }
  },
  created() {
    this.getDataFromUrl();
  },

}



