
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Card from 'vux/src/components/card/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import {
global
} from '../assets/javascript/global.js'
import {
request
} from '../api/api.js'
import store from '../store.js'
export default {
components: {
Tab,
TabItem,
Toast,
Card
},
data() {
return {
pageSize: global.pageSize,
page: 1,
stage: 2,
showMsg: false,
isNoData: false,
text: '',
type: '',
coaches: [],
userInfo: JSON.parse(sessionStorage.getItem('token')),
}
},
methods: {
onItemClick(index) {
let state = index === 0 ? 2 : 3;


if (this.stage !== state) {
this.stage = state;
this.isNoData = false;
store.commit('LOADING');
this.getReservedCoach();
}

},
goRouteInfo(param) {

param.stage = this.stage;
this.$router.push({
path: '/reservationInfo',
query: {
teacherId: param.teacherId,
stage: param.stage
}
});

},
getReservedCoach() {
this.coaches = [];
if (this.userInfo) {
let args = [this.userInfo.schoolCode, this.userInfo.studentId, this.page, this.pageSize, this.stage];

request.Appointment.queryAppointmentTeacherBind(args).then(res => {

if (res.success) {
if (res.object.list.length) {
store.commit("LOADED");
res.object.list.forEach(item => {
this.coaches.push({
appointmentCountThree: item.appointmentCountThree,
appointmentCountTwo: item.appointmentCountTwo,
bindCar: item.bindCar,
evaCount: item.evaCount,
evaScore: item.evaScore,
carType: item.name,
photosUrl: item.photosUrl,
schoolCode: item.schoolCode,
stage: item.stage,
teacherId: item.teacherId,
teacherName: item.teacherName
});
});
} else {
this.isNoData = true;
store.commit('LOADED');
}
} else {
this.type = 'text';
this.showMsg = true;
this.text = res.message;
store.commit("LOADED");
}
});
} else {
this.type = 'warn';
this.text = '请先登录';
this.showMsg = true;
store.commit("LOADED");
}
}
},
created() {

this.getReservedCoach();


}
}



