
import Scroller from 'vux/src/components/scroller/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'

import {
request
} from '../api/api.js'
import {
global
} from '../assets/javascript/global.js'
import store from '../store.js'
export default {
components: {
Scroller,
Group,
Cell,
XButton,
Confirm,
Spinner,
Toast
},
computed: {
isAll: function() {
return this.page > this.totalPage
}
},
data() {
return {
pageSize: global.pageSize,
appointmentList: [],
onFetching: false,
showMsg: false,
showWarn: false,
isNoData: false,
page: 1,
pullupConfig: {
content: "",
downContent: "松开进行加载",
upContent: "上拉加载更多",
loadingContent: "加载中..."
},
pulldownConfig: {
content: "下拉刷新",
downContent: "下拉刷新",
upContent: "释放刷新",
loadingContent: "加载中..."
},
status: {
pullupStatus: 'default',
pulldownStatus: 'default'
},
totalPage: 1,
text: '',
type: '',
willCancelAppId: '',
willCancelIndex: -1,
userInfo: JSON.parse(sessionStorage.getItem('token')),
deadlineMsg: ''
}
},
methods: {
onCancel() {
this.showWarn = false;
},
onConfirm() {
let args = {
cancelUserId: Number(this.userInfo.studentId),
appointmentId: Number(this.willCancelAppId),
cancelUserType: 10
};
request.Appointment.cancelAppointment(args).then(res => {
if (res.success) {

this.type = 'success';
this.text = res.object.message;
this.showMsg = true;
this.appointmentList.splice(this.willCancelIndex, 1);
if (!this.appointmentList.length) this.isNoData = true;
} else {
this.type = 'text';
this.text = res.message;
this.showMsg = true;
}
});
},
addAppointment() {
this.$router.push({
path: '/reservation'
})
},
onItemClick(data, index) {
console.log(index);
sessionStorage.setItem('yyInfo', JSON.stringify(data));
if (data.tag === '可取消') {
console.log('可取消');
this.showWarn = true;
this.willCancelAppId = data.appointmentId;
this.willCancelIndex = index;
} else if (data.tag === '待支付') {
this.type = 'text';
this.text = '未支付';
this.showMsg = true;
} else if (data.tag === '未开始' || data.tag === '培训中') {
return
} else {

if (data.isEvaluate) {
this.$router.push({
path: '/evaluationResult',
query: {
teacherId: data.teacherId,
appointmentId: data.appointmentId
}
});
} else {

this.$router.push({
path: '/evaluation',
query: {
studentId: data.studentId,
teacherId: data.teacherId
}
});
}
}
},

onPullUpLoad() {
setTimeout(() => {

if (this.page < this.totalPage) {
this.page++;
this.loadText = '上拉加载更多'
this.getReservationData();
} else {

this.page += 1;
this.$refs.scrollerBottom.disablePullup();
this.$nextTick(() => {

this.deadlineMsg = '已加载全部信息';
});
}
}, 1000);
},

onPullDownLoad() {

this.getReservationData('updata');
setTimeout(() => {
this.$nextTick(() => {
this.page = 1;
this.$refs.scrollerBottom.donePulldown();

this.page < this.totalPage ? this.$refs.scrollerBottom.enablePullup() : this.$refs.scrollerBottom.disablePullup();
this.status.pulldownStatus = "default";
this.deadlineMsg = '已加载全部信息';
});
}, 1000);






},












getReservationData(type) {



if (this.userInfo) {
let args = [this.userInfo.schoolCode, this.userInfo.studentId, this.page, this.pageSize];

request.Appointment.appointmentList(args).then(res => {

if (res.success) {
this.totalPage = res.object.totalPage;
if (res.object.list.length) {
store.commit('LOADED');
if (type === 'updata') {
this.appointmentList = [];
this.text = '刷新成功';
this.type = 'text';
this.showMsg = true;
}
res.object.list.forEach(item => {
this.appointmentList.push({
appointmentDate: `${new Date(item.appointmentDate).toLocaleDateString().replace(/\//g, '.')} 星期${this.switchType(new Date(item.appointmentDate).getDay(), true)}`,
appointmentId: item.appointmentId,
appointmentStage: this.switchType(item.appointmentStage, false),
nativeStage: item.appointmentStage,
beginTime: new Date(item.beginTime).toTimeString().slice(0, 8),
endTime: new Date(item.endTime).toTimeString().slice(0, 8),
id: item.id,
isComplaint: item.isComplaint,
isEvaluate: item.isEvaluate,
isLearnFirst: item.isLearnFirst,
isPay: item.isPay,
photosUrl: item.photosUrl,
schoolName: item.schoolName,
state: item.state,
studentId: item.studentId,
tag: item.tag,
teacherId: item.teacherId,
teacherName: item.teacherName,
buttonType: item.tag === '未支付' ? 'warn' : (item.tag === '可取消' ? 'default' : 'primary')
});
});

} else {
this.isNoData = true;
store.commit('LOADED');
type === 'updata' ? this.text = '刷新成功,暂时没有数据' : this.text = '没有更多数据'
this.type = 'text';
this.showMsg = true;
}
setTimeout(() => {
this.$refs.scrollerBottom.donePullup();
}, 100);
} else {
store.commit('LOADED');
this.type = 'warn';
this.text = `数据请求失败，原因： ${res.message}`;
this.showMsg = true;
}
});
} else {
store.commit('LOADED');
this.type = 'warn';
this.text = '请先登录';
this.showMsg = true;
}
},
switchType(param, type) {
let newChar = '';

if (type) {
switch (param) {
case 1:
newChar = '一';
break;
case 2:
newChar = '二';
break;
case 3:
newChar = '三';
break;
case 4:
newChar = '四';
break;
case 5:
newChar = '五';
break;
case 6:
newChar = '六';
break;
case 7:
newChar = '日';
break;
default:
break;
}
} else {
switch (param) {
case 1:
newChar = '科目一';
break;
case 2:
newChar = '科目二';
break;
case 3:
newChar = '科目三';
break;
case 4:
newChar = '科目四';
break;
default:
break;
}
}
return newChar;
}
},
created() {


this.appointmentList = [];
this.getReservationData();
}
}



