
import Toast from 'vux/src/components/toast/index.vue'
import Rater from 'vux/src/components/rater/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'

import {
global
} from '../assets/javascript/global.js'
import {
request
} from '../api/api.js'
import store from '../store.js'
export default {
components: {
Toast,
Rater,
Group,
Checklist,
XButton,
XTextarea,
Checker,
CheckerItem
},
data() {
return {
rater: 0,
selfRater: 0,
commentsTag: [],
yyInfo: JSON.parse(sessionStorage.getItem('yyInfo')),








anonymous: false,
showMsg: false,
text: '',
type:'',
comments: '',
teacherInfo: {},
userInfo:JSON.parse(sessionStorage.getItem('token'))
}
},
methods: {
onItemClick(data) {

},
getCommentsData() {
let querys = this.$route.query;
if(this.userInfo){
const args = [this.userInfo.schoolCode, querys.teacherId, querys.appointmentId];

request.Appointment.evaResult(args).then(res => {

if (res.success) {
store.commit('LOADED');
this.teacherInfo = res.object.evaTeacherInfo;
let commentTag = res.object.baseEvaluate.evaluateList.split('|');
if(commentTag.length){
commentTag.forEach((item, index) => {
this.commentsTag.push({
key: index,
value: item
});
});
}
this.selfRater = res.object.baseEvaluate.score;
this.rater = res.object.evaTeacherInfo.tscore / res.object.evaTeacherInfo.tcount;
this.comments = res.object.baseEvaluate.comments;
} else {
store.commit('LOADED');
this.type = 'text';
this.text = res.message;
this.showMsg = true;
}
});
}else{
store.commit('LOADED');
this.type = 'warn';
this.text = '请先登录';
this.showMsg = true;
}
}
},
created() {

this.getCommentsData();
}
}



