
import Toast from 'vux/src/components/toast/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import Rater from 'vux/src/components/rater/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'

import {
global
} from '../assets/javascript/global.js'
import {
request
} from '../api/api.js'
import store from '../store.js'
export default {
components: {
Tab,
Toast,
Rater,
Checker,
CheckIcon,
CheckerItem,
XTextarea,
XButton
},
data() {
return {
showMsg: false,
editRater: 0,
text: '',
type:'',
goodTag: [],
middleTag: [],
badTag: [],
yyInfo: JSON.parse(sessionStorage.getItem('yyInfo')),









schoolCode: global.schoolCodes,
studentId: global.studentId,
pageSize: global.pageSize,
page: 1,
commentsTag: [],
comments: '',
anonymous: false,
teacherInfo: {},
userInfo: JSON.parse(sessionStorage.getItem('token')),
originHeight: document.body.clientHeight,//默认高度在watch里作比较
isOrigin:true,
tmp:''
}
},
computed: {
rater: {
get: function () {
return this.teacherInfo.tscore / this.teacherInfo.tcount
},
set: function () {
return this.teacherInfo.tscore / this.teacherInfo.tcount;
}
}
},
watch: {
tmp (val,oldVal) {

if(this.originHeight !== val) {
this.isOrigin = false;
}else{
this.isOrigin = true;
}




}
},
methods: {
onItemClick(data) {

},
getTeacherInfo() {

if (this.userInfo) {
const args = [this.userInfo.schoolCode, this.$route.query.teacherId];

setTimeout(() => {
request.Appointment.evaTeacherInfo(args).then(res => {
global.printLog(res);
if (res.success) {
store.commit('LOADED');
this.teacherInfo = res.object.teacherInfo;
if(res.object.tagList.length){
res.object.tagList.forEach(item => {
if (item.typeId === 29) {
this.goodTag.push({
key: item.id,
value: item.name
});
} else if (item.typeId === 30) {
this.middleTag.push({
key: item.id,
value: item.name
});
} else if (item.typeId === 31) {
this.badTag.push({
key: item.id,
value: item.name
});
}
});
}
} else {
store.commit('LOADED');
this.type = 'warn';
this.text = res.message;
this.showMsg = true;
}
});
});
}else{
this.type = 'warn';
this.text = '请先登录';
this.showMsg = true;
store.commit('LOADED');
}
},
commentUp() {
if (this.userInfo) {
let evaluate = '';
this.commentsTag.forEach((item, index) => {
if (index === 0) evaluate += item.value;
else if (index > 0 && index < this.commentsTag.length) {
evaluate += `| ${item.value}`;
}
});
if(evaluate ===''){
this.type = 'text';
this.showMsg = true;
this.text = '请选择评价标签';
}else{
var args = {
"schoolCode": Number(this.userInfo.schoolCode),
"studentId": Number(this.userInfo.studentId),
"studentName": this.userInfo.studentName.replace(/'/g,''),
"studentIdNumber":this.userInfo.cardNo.replace(/'/g,''),
"evaObjectId": Number(this.teacherInfo.teacherId),
"score": Number(this.editRater),
"stage": Number(this.yyInfo.nativeStage),
"evaluateList": evaluate,
"comments": this.comments,
"appointmentId": Number(this.yyInfo.appointmentId),
"isNickName": Number(this.anonymous ? '1' : '2')
};

request.Appointment.addEvaluate(args).then(res => {
if (res.success) {
this.type = 'success';
this.text = '评论成功~';
this.showMsg = true;
setTimeout(()=>{
this.$router.replace({
path: '/',
});
},2000);
} else {
this.type = 'text';
this.text = res.message;
this.showMsg = true;
}
this.comments = '';
this.editRater = 5;
this.commentsTag = [];
});
}
}else{
this.type = 'warn';
this.text = '请先登录';
this.showMsg = true;
}
}
},
created() {

this.getTeacherInfo();
},
mounted(){
window.onresize = () => {
this.tmp = document.body.clientHeight;





}
}
}



